import * as React from 'react';
import type { NextPage } from 'next';
import { Container, Box, Typography, TextField, Button, Grid, Link as MuiLink } from '@mui/material';
import { useForm, SubmitHandler } from 'react-hook-form';
import Link from 'next/link';

// Define the shape of our form data
type LoginFormInputs = {
  email: string;
  pass: string;
};

const LoginPage: NextPage = () => {
  const { register, handleSubmit, formState: { errors } } = useForm<LoginFormInputs>();

  // This function runs when the form is submitted
  const onSubmit: SubmitHandler<LoginFormInputs> = (data) => {
    console.log(data);
    // TODO: Send this data to the backend API for authentication
  };

  return (
    <Container component="main" maxWidth="xs">
      <Box
        sx={{
          marginTop: 8,
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
        }}
      >
        <Typography component="h1" variant="h5">
          Sign In
        </Typography>
        <Box component="form" onSubmit={handleSubmit(onSubmit)} noValidate sx={{ mt: 1 }}>
          <TextField
            margin="normal"
            required
            fullWidth
            id="email"
            label="Email Address"
            autoComplete="email"
            autoFocus
            // Register this input with react-hook-form
            {...register("email", { 
              required: "Email is required",
              pattern: {
                value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                message: "Invalid email address"
              }
            })}
            // Show validation errors
            error={!!errors.email}
            helperText={errors.email?.message}
          />
          <TextField
            margin="normal"
            required
            fullWidth
            label="Password"
            type="password"
            id="pass"
            autoComplete="current-password"
            // Register this input
            {...register("pass", { 
              required: "Password is required",
              minLength: {
                value: 6,
                message: "Password must be at least 6 characters"
              }
            })}
            error={!!errors.pass}
            helperText={errors.pass?.message}
          />
          {/* (We can add a "Remember me" checkbox here later) */}
          <Button
            type="submit"
            fullWidth
            variant="contained"
            sx={{ mt: 3, mb: 2 }}
          >
            Sign In
          </Button>
          <Grid container>
            {/* @ts-ignore */}
            <Grid item>
              <Link href="/auth/register" passHref>
                <MuiLink variant="body2">
                  {"Don't have an account? Sign Up"}
                </MuiLink>
              </Link>
            </Grid>
          </Grid>
        </Box>
      </Box>
    </Container>
  );
};

export default LoginPage;